.containerShare {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  font-family: "Open Sans", sans-serif;
  color: #54585A;
  font-size: 18px;
  width: 100%;
}

.containerShare .subContainerShare {
  display: flex;
  flex-direction: row;
  font-family: "Open Sans", sans-serif;
  color: #54585A;
  font-size: 18px;
}

.containerShare .subContainerShare .icon {
  padding-top: 10px;
}

.containerShare .subContainerShare .textDesign {
  padding-top: 10px;
}

.containerShare .subContainer2Share .iconDirection {
  padding-top: 10px;
  padding-right: 20px;
}

.createCards {
  max-height: 0px;
  transition: max-height 0.5s;
  overflow: hidden;
  margin: auto;
}

.createCards .send {
  margin-top: 30px;
  margin-bottom: 30px;
  background-color: #e17334;
  border-radius: 12px;
  width: 199px;
  height: 50px;
}

.createCards .send .iconSend {
  width: 22px;
  height: 25px;
  color: white;
}

.createCards .send .textButton {
  font-family: "Open Sans", sans-serif;
  font-size: 18px;
  color: white;
  padding-left: 9px;
}

.preview {
  background: url("../images/shiny.jpg") top center no-repeat;
  background-size: cover;
  height: 80vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.preview .reset {
  width: 75px;
  height: 30px;
  margin-top: 20px;
  margin-bottom: 20px;
  align-self: center;
  transform: translateX(-85px);
  border: solid 1px #ffffff;
  border-radius: 15px;
  background-color: transparent;
  font-family: "Open Sans", sans-serif;
  font-size: 12px;
  line-height: 1.67;
  color: #54585a;
  text-transform: uppercase;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
}

.preview .reset:hover {
  border: 2px solid #114E4E;
}

.preview .card {
  height: 400px;
  width: 270px;
  border-radius: 2px;
  background-color: rgba(17, 78, 78, 0.8);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 20px 30px 40px 30px;
  background-color: #ffffff;
  width: 240px;
  height: 329px;
}

.preview .card .headline {
  align-self: flex-start;
  margin-top: 23px;
  margin-bottom: 10px;
  align-items: center;
}

.preview .card .headline .name {
  text-transform: capitalize;
  font-family: "Montserrat", sans-serif;
  font-weight: "Montserrat", sans-serif;
  font-size: 1.125rem;
  line-height: 20px;
  color: #114E4E;
  padding-left: 20px;
  margin-top: 0px;
  margin-bottom: 2px;
}

.preview .card .headline .role {
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: #54585A;
  padding-left: 20px;
  margin-top: 2px;
  margin-bottom: 0px;
}

.preview .card .image {
  background: url("../images/queen.gif") top center no-repeat;
  background-size: cover;
  width: 240px;
  height: 200px;
}

.socialKit {
  display: flex;
  justify-content: center;
  align-items: center;
  border: 4px;
  height: 50px;
  width: 100%;
  padding-right: 62px;
  padding-left: 20px;
  margin-top: 7px;
}

.socialKit .list {
  height: 32px;
  opacity: 0.6;
  list-style: none;
}

.socialKit .list .btn {
  border: solid 2px green;
  border-radius: 50%;
  width: 32px;
  height: 32px;
  margin: 0px 5px 15px 5px;
  background-color: transparent;
  display: flex;
  justify-content: center;
  align-items: center;
}

.colorPalette1 .headline {
  border-left: solid 5px #438792;
}

.colorPalette1 .headline .name {
  color: #114E4E;
}

.colorPalette2 .headline {
  border-left: solid 5px #bd1010;
}

.colorPalette2 .headline .name {
  color: #420101;
}

.colorPalette3 .headline {
  border-left: solid 5px #eab052;
}

.colorPalette3 .headline .name {
  color: #3e5b65;
}

.action {
  max-height: 700px;
}

.arrow-rotate {
  transform: rotate(180deg);
}

.arrow-rotate:hover {
  color: #e17334;
}

@media all and (min-width: 768px) {
  .landing__container {
    height: 90%;
  }

  .landing__container .container_logo {
    padding: 150px 174px 31px 175px;
  }

  .landing__container .container_logo .logo {
    width: 419px;
  }

  .landing__container .main .main__title {
    font-size: 24px;
    width: 278px;
    height: 31px;
  }

  .landing__container .main .main__content {
    width: 331px;
    height: 48px;
    font-size: 16px;
  }

  .landing__container .main .main__icons {
    grid-template-columns: 108px 108px 108px;
    grid-template-rows: 86px;
    width: 325px;
    height: 86px;
  }

  .landing__container .main .main__icons i {
    height: 29px;
    font-size: 28px;
  }

  .landing__container .main .main__text--icons {
    font-size: 16px;
  }

  .landing__container .main__button {
    margin-top: 70px;
  }

  footer .footer-wrapper {
    height: 60px;
    padding: 10px 40px;
    flex-direction: row;
    justify-content: space-between;
  }

  footer .footer-wrapper .copyright {
    margin-top: 0px;
  }

  .cardWrapper {
    display: flex;
    flex-direction: row-reverse;
    justify-content: center;
    padding: 0 40px;
    background-image: linear-gradient(to bottom, #f5f5f5, #ffffff 9%);
    padding-top: 20px;
  }
}
