.cardWrapper {
    @include forTablet{
    display: flex;
    flex-direction: row-reverse;
    justify-content: center;
    // align-items: baseline;
    padding: 0 40px;
    background-image: linear-gradient(to bottom, #f5f5f5, #ffffff 9%);
    padding-top: 20px;
    }
  }
  
  .preview {
    background: url("../images/shiny.jpg") top center no-repeat;
    background-size: cover;
    height: 80vh;
    display: flex;
    justify-content: center;
    align-items: center;
  
    .reset {
      width: 75px;
      height: 30px;
      margin-top: 20px;
      margin-bottom: 20px;
      align-self: center;
      transform: translateX(-85px);
      border: solid 1px $whiteColor;
      border-radius: 15px;
      background-color: transparent;
      font-family: $primaryFont;
      font-size: 12px;
      line-height: 1.67;
      color: #54585a;
      text-transform: uppercase;
      cursor: pointer;
      display: flex;
      justify-content: center;
      align-items: center;
      &:hover {
        border: 2px solid $headerColor;
      }
    }
  
    .card {
      height: 400px;
      width: 270px;
      border-radius: 2px;
      background-color: rgba(17, 78, 78, 0.8);
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
  
      margin: 20px 30px 40px 30px;
      background-color: $whiteColor;
      width: 240px;
      height: 329px;
  
      .headline {
        // border-left: 5px solid $headerColor; debe comentarse porque sino pisa el css del javascript
        align-self: flex-start;
        margin-top: 23px;
        margin-bottom: 10px;
        align-items: center;
  
        .name {
          text-transform: capitalize;
          font-family: $cardFont;
          font-weight: $cardBoldFont;
          font-size: 1.125rem;
          line-height: 20px;
          color: $headerColor;
          padding-left: 20px;
          margin-top: 0px;
          margin-bottom: 2px;
        }
  
        .role {
          font-family: $cardFont;
          font-weight: 400;
          font-size: 16px;
          line-height: 20px;
          color: $metalColor;
          padding-left: 20px;
          margin-top: 2px;
          margin-bottom: 0px;
        }
      }
      .image {
        background: url("../images/queen.gif") top center no-repeat;
        background-size: cover;
        width: 240px;
        height: 200px;
      }
    }
  }
  
  .socialKit {
    display: flex;
    // align-content: flex-start;
    justify-content: center;
    align-items: center;
    border: 4px;
    height: 50px;
    width: 100%;
    padding-right: 62px;
    padding-left: 20px;
    margin-top: 7px;
  
    .list {
      height: 32px;
      opacity: 0.6;
      list-style: none;
  
      .btn {
        border: solid 2px green;
        border-radius: 50%;
        width: 32px;
        height: 32px;
        margin: 0px 5px 15px 5px;
        background-color: transparent;
        display: flex;
        justify-content: center;
        align-items: center;
      }
    }
  }
  